
.org-svg {
  position: relative;
  margin: 0 -15px;
}
.org-svg .zoom-slider {
  position: fixed;
  bottom: 20px;
  right: 50px;
  width: 200px;
  -ms-transform: rotate(90deg);
      transform: rotateZ(90deg);
  -ms-transform-origin: bottom right;
      transform-origin: bottom right;
}
.org-svg .zoom-slider .zoom {
  -ms-flex: 1;
      flex: 1;
}
.org-svg .node {
  cursor: pointer;
}
.org-svg .node .top-text {
  color: #1ab394;
}
.org-svg .node .setting,
.org-svg .node .setting-text {
  display: none;
}
.org-svg .node .setting-text {
  font: normal normal normal 14px/1 FontAwesome !important;
}
.org-svg .node .setting-text::after {
  content: "\f007";
}
.org-svg .node .bottom-text {
  display: block;
  font-size: 12px;
}
.org-svg .node:hover rect {
  stroke: #1ab394;
}
.org-svg .node:hover .setting,
.org-svg .node:hover .setting-text {
  display: block;
}
.org-svg .node:hover .bottom-text {
  display: none;
}
.org-svg .node circle {
  fill: #fff;
  stroke: steelblue;
  stroke-width: 1px;
}
.org-svg .node text {
  font-size: 13px;
  font-weight: 300;
}
.org-svg .link {
  fill: none;
}
.org-svg #infovis {
  overflow: hidden;
}
.org-svg .fixed-box {
  z-index: 100;
  background: #fff;
  overflow: auto;
  visibility: hidden;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
.org-svg .fixed-box.active {
  visibility: visible;
}
.org-svg .fixed-box-close {
  z-index: 101;
  background: #eee;
  padding: 10px;
  visibility: hidden;
  position: fixed;
  top: 20px;
  left: 20px;
}
.org-svg .fixed-box-close.active {
  visibility: visible;
}



